import React from 'react';
import './index.less';
import { CommonProps } from '@interface';

export default function Tags(props: CommonProps) {
    const {
        schema: { className = '', logInfo: { itemClickType = '' } = {} },
        style = {},
        info: { exposeTagInfoList = [] },
    } = props;

    const tagList = exposeTagInfoList.map((item) => item?.text);

    return (
        <>
            {tagList.length ? (
                <div
                    className={`tags-list ${className}`}
                    data-itemClickType={itemClickType}
                    style={style}
                >
                    {tagList.map((item, index) => (
                        <div
                            className="tags-item"
                            key={item}
                            style={{ borderRadius: style['border-radius'] }}
                        >
                            {item}
                        </div>
                    ))}
                </div>
            ) : null}
        </>
    );
}
